<template>
  <div class="hello">
      <div class="gonggao_bread">
        <a href="#" class="fa"></a>
        &gt;
        <span></span>
        &gt;
        <span></span>
      </div>
      <div class="search_div">
        <label></label>
        <select v-model="searchData.searchDateVal">
          <option v-for="d in searchData.searchDate" v-bind:value="d">{{ d }}</option>
        </select>
        <label></label>
        <select v-model="searchData.areaVal">
          <option v-for="a in searchData.areaList" v-bind:value="a.areaId">{{ a.areaName }}</option>
        </select>
      </div>

      <div class="gonggao_body">
        <div class="money_table" v-for="(v, k) in tableData">
          <table cellpadding="0" cellspacing="0" border="1">
            <thead>
              <tr>
                <td width="10%" align="center" style="cursor:pointer;" v-on:mouseenter="showProList(k)" v-on:mouseleave="hideProList(k)">
                  <Icon type="chevron-down" style="margin-left:3px;"></Icon>
                  <ul class="person_ul" v-if="dropData.provincePause[k].sp" >
                    <li v-for="p in dropData.provinceList" v-bind:pid="p.provinceId" v-on:click="setSecProvinceId(k, p.provinceId, p.province)">{{ p.province }}</li>
                  </ul>
                </td>
                <td width="10%" align="center" style="cursor:pointer;" v-on:mouseenter="showMonTypeList(k)" v-on:mouseleave="hideMonTypeList(k)">
                  <Icon type="chevron-down" style="margin-left:3px;"></Icon>
                  <ul class="person_ul edu" v-if="dropData.monTypePause[k].mp">
                    <li v-for="(m, m1) in dropData.monTypeList">
                      <input type="checkbox" v-bind:value="m.costTypeId" v-model="dropData.monTypeModel[k]" v-bind:id="'edu'+m1"/>
                      <label v-bind:for="'edu'+m1">{{ m.costTypeName }}</label>
                    </li>
                  </ul>
                </td>
                <td width="16%"></td>
                <td width="10%"></td>
                <td width="10%"></td>
                <td width="22%"></td>
                <td width="22%"></td>
              </tr>
            </thead>
            <tbody>
              <tr style="height:40px;" v-for="(t, i) in v.sun">
                <td align="center" v-if="i==0" v-bind:rowspan="v.sun.length">{{ dropData.selectedProvince[k].name }}</td>
                <td align="center">{{ t["c2"] }}</td>
                <td>
                  {{ (t["c5"]!='')?(t["c1"]==2?t["c5"].join(","):t["c5"]):'' }}
                  <div class="xiaopen" v-if="dropData.penShow[k].ps" v-on:click.stop="spanShowSchool(i, k)">
                    <Icon type="compose"></Icon>
                  </div>
                  
                  <ul class="person_ul edu" v-if="t.c3==true">
                    <li style="text-align:center;">
                      <select v-model="t.c12" v-on:change="getSchoolList(k, i)">
                        <option v-for="c in dropData.cityList" v-bind:value="c.cityId">{{ c.cityName }}</option>
                      </select>
                    </li>
                    <li v-if="(t.c1!=2)&&(dropData.schoolList.length>0)" v-for="(s, s1) in dropData.schoolList">
                      <input type="radio" name="sc" v-if="s.customerId!=''" v-model="t.c4" v-bind:value="s.customerId" v-bind:id="'sc'+s1"/>
                      <label v-bind:for="'sc'+s1">{{ s.customerName }}</label>
                    </li>
                    <li v-if="(t.c1==2)&&(dropData.schoolList.length>0)" v-for="(s, s1) in dropData.schoolList">
                      <input type="checkbox" v-if="s.customerId!=''" v-model="dropData.selectSchoolModelDuo[k]['sm']" v-bind:value="s.customerId" v-bind:id="'sc'+s1"/>
                      <label v-bind:for="'sc'+s1">{{ s.customerName }}</label>
                    </li>
                    <li style="text-align:center;" v-if="dropData.schoolList.length>0">
                      <span class="btn-blue-s gx_btn" v-on:click="fuSchoolVal(i, k)"></span>
                    </li>
                  </ul>

                </td>
                <td>
                  {{ (t.c1!=2)?t["c8"]:'N/A' }}
                  <div class="xiaopen" v-if="(t.c1!=2)&&dropData.penShow[k].ps" v-on:click.stop="spanLinkMan(i, k)">
                    <Icon type="compose"></Icon>
                  </div>
                  <ul class="person_ul edu" v-if="t.c6==true">
                    <li v-for="(l, l1) in dropData.linkManList">
                      <input type="radio" name="kh" v-if="l.customerContactsId!=''" v-bind:value="l.customerContactsId" v-model="t.c7" v-bind:id="'kh'+l1"/>
                      <label v-bind:for="'kh'+l1">{{ l.contactsName }}</label>
                    </li>
                    <li style="text-align:center;">
                      <span class="btn-blue-s gx_btn" v-on:click="fuLinkMan(i, k)"></span>
                    </li>
                  </ul>
                </td>

                <td>

                  <input type="text" v-if="(t.c1!=2)&&dropData.penShow[k].ps" v-model="t.c9.edval" v-on:keyup="valiEdu(t)" style="width:100px;"/>
                  <span v-if="t.c1==2">{{ t.c9.edval }}</span>
                  <div class="xiaopen" v-if="(t.c1==2)" v-on:click.stop="spanChailv(i, k)">
                    <Icon type="compose"></Icon>
                  </div>

                </td>

                <td>
                  <textarea class="tt" v-model="t.c10"></textarea>
                </td>

                <td>
                  <textarea class="tt" v-model="t.c11"></textarea>
                </td>
              </tr>
            </tbody>
          </table>

          <div style="text-align:right;margin-top:10px;">
            <span class="btn-yellow-s" v-on:click="removeTableData(k)" v-if="(tableData.length>1) && k!=0"></span>
            <span class="btn-blue-s" v-on:click="addNewTableData" style="margin-right:0;" v-if="k==(tableData.length-1)"></span>
          </div>

        </div>
        <div style="text-align:right;margin:0 20px;font-size:16px;">
          {{ subMoney }}
        </div>
        <div class="fbtong_btn_div" style="padding-bottom:30px;">
          <span class="btn-yellow" v-on:click="viewPersonApply"></span>
          <span class="btn-blue" v-on:click="subPersonApply"></span>
        </div>

      </div>

      <div class="bgpop" v-for="(e, ei) in ed" v-bind:class="{'check': e.shCheck}"></div>
      <div class="view_area" v-for="(e, ei) in ed" style="width: 600px;height:236px;padding: 5px 20px;" v-bind:class="{'check': e.shCheck}">
        <div style="font-size: 20px;font-weight: 700;text-align: center;margin-top: 10px;"></div>
        <div class="view_school" style="margin-top: 10px;">
          <label style="display: inline-block;width: 70px;text-align: right;"></label>

          <el-date-picker
            v-model="e.startTime"
            type="date"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            :picker-options="pickerOptions"
            placeholder="">
          </el-date-picker>

          <label style="display:inline-block;margin:10px 5px;"></label>
          
          <el-date-picker
            v-model="e.endTime"
            type="date"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            :picker-options="pickerOptions"
            placeholder="">
          </el-date-picker>
          
        </div>
        <div class="view_school" style="margin-top: 10px;">
          <label style="display: inline-block;width:70px;text-align:right;"></label>
          <input type="text" v-model="e.glf" v-on:keyup="glfKeyup(ei)"/>
          <label style="display: inline-block;width: 70px;text-align:right;"></label>
          <input type="text" v-model="e.yf" v-on:keyup="yfKeyup(ei)"/>
        </div>
        <div style="text-align:center;color:red;margin-top:10px;height:20px;">{{ e.errorTip }}</div>
        <div class="view_btn" style="margin-top:20px;">
          <span class="btn-blue-s" v-on:click="countEduFee(ei)"></span>
        </div>
      </div>

      <div class="bgpop" v-bind:class="{'check': viewData.shCheck}"></div>
      <div class="view_area" style="width:1024px;height:400px;overflow-y:auto;"
       v-bind:class="{'check': viewData.shCheck}">
        <div style="margin:20px;border:1px solid #DDD;padding:5px;">
          <p>{{ searchData.searchDateVal }}</p>
          <p></p>
          <p>{{ viewData.areaData }}-{{ viewData.personProvince }}-{{ viewData.userName }}</p>
        </div>
        <div class="view_eve_table_div" v-for="(vi, vk) in tableData">
          <table cellpadding="0" cellspacing="0" border="1">
            <thead>
              <tr>
                <td width="10%"></td>
                <td width="10%"></td>
                <td width="15%"></td>
                <td width="10%"></td>
                <td width="10%"></td>
                <td width="23%"></td>
                <td width="21%"></td>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(vr, vu) in vi.sun">
                <td v-bind:rowspan="vi.sun.length" v-if="vu==0">{{ dropData.selectedProvince[vk]["name"] }}</td>
                <td>{{ vr.c2 }}</td>
                <td>{{ (vr.c5!='')?((vr.c1==2)?(vr.c5.join(",")):vr.c5):vr.c5 }}</td>
                <td>{{ (vr.c1!=2)?vr["c8"]:'N/A' }}</td>
                <td>{{ vr.c9.edval }}</td>
                <td>{{ vr.c10 }}</td>
                <td>{{ vr.c11 }}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="view_btn" style="margin-top:20px;">
          <span class="btn-blue-s" v-on:click="closeViewPop"></span>
        </div>
      </div>

      <oa-alert ref="childAlert"></oa-alert>
  </div>
</template>

<script>
import $ from 'jquery'
import oaAlert from '@/components/commonVue/Alert'

export default {
  name: 'PersonApply',
  components: {
      'oa-alert': oaAlert
    },
  data () {
    return {
      path: this.OACommon["publicDef"]["qpath"],
      pickerOptions: {
        firstDayOfWeek: 1,
        disabledDate(time) {
            return time.getTime() < Date.now();
        }
      },
      ccbz: 350,                    //
      costId: '',
      searchData: {
        searchDate: [],
        searchDateVal: '',
        areaList: [],
        areaVal: ''
      },
      dropData: {
        provincePause: [{"sp":false}],
        provinceList: [],            //
        provinceId: [{"id": ''}],              //id
        selectedProvince: [{"name": ''}],        //name
        monTypePause: [{"mp": false}],
        monTypeList: [],             //
        monTypeModel: [[]],             //id
        penShow: [{"ps": false}],
        schoolList: [],                //
        //selectSchoolModel: [{"sm": [{"sg": ''}]}],         //
        selectSchoolModelDuo: [{"sm": []}],
        pushSchoolModel: [],           //push
        cityList: [],                    //
        cityId: '',                       //id
        linkManList: [{"customerContactsId": "", "contactsName": ""}],                 //
        linkManModel: [{"link": [{"lk": ''}]}]
      },
      tableData: [{
                    sun: [
                      {
                        "c1": '',
                        "c2": '',
                        "c3": false,
                        "c4": '',
                        "c5": '',
                        "c6": false,
                        "c7": '',
                        "c8": '',
                        "c9": {"edval": '', edObj: {}},
                        "c10": '',
                        "c11": '',
                        "c12": ''
                      }
                    ]             
                  }
                ],                //1id23 4id 5 6: 7id 
                            //8name  9:  10:  11  12:id
      ed: [{
        shCheck: true,
        startTime: '',
        endTime: '',
        glf: '',
        yf: '',
        errorTip: '',
        popd: '',
        popdK: ''
      }],
      subPause: true,
      subMoney: 0,
      viewData: {             // 
        areaData: '',
        userName: localStorage.getItem("userName"),
        shCheck: true,
        personProvince: ''
      }
    }
  },
  created: function () {
    //this.costJudgementTime();    //25-31
    this.costId = this.OACommon.getUrlPara("id");   //id
    this.setSearchData();        //
    this.getProvinceList();      //
    this.getSelectCostType();    //
    if(this.costId){
      this.getOldData();
    }
  },
  methods: {
    getOldData: function(){        //
      $.ajax({
        url: this.path + "selectFeeApplicationDetail",
        type: "post",
        dataType: "json",
        data: {
          "jobNumber": localStorage.getItem("jobNumber"),
          "sessionIdOa": localStorage.getItem("sessionIdOa"),
          "id": this.costId           
        },
        success: function(data){
          data = data["data"][0];

          this.subMoney = data["money"];
          //
          this.searchData.searchDateVal = data["spendtimeString"].slice(0, -3);
          for(var q=0;q<this.searchData.areaList.length;q++){
            if(this.searchData.areaList[q]["areaName"] == data["areaName"]){
              this.searchData.areaVal = this.searchData.areaList[q]["areaId"];
            }
          }
          //
          var newData = {}, oldData = data["costDetailVoList"], ol = oldData.length;
          for(var i=0;i<ol;i++){
            var esign = oldData[i]["sign"];
            if(!newData[esign]){
              newData[esign] = [];
            }
          }
          for(var j=0;j<ol;j++){
            var esign = oldData[j]["sign"];
            newData[esign].push(oldData[j]);
          }
          //
          this.dropData.provincePause = [];
          this.dropData.provinceId = [];
          this.dropData.selectedProvince = [];
          this.dropData.monTypePause = [];
          this.dropData.monTypeModel = [];
          this.dropData.penShow = [];
          this.dropData.selectSchoolModelDuo = [];
          this.dropData.linkManModel = [];
          this.ed = [];
          this.tableData = [];
          for(var o in newData){
            var oo = Number(o);         //num
            //
            this.dropData.provincePause.push({"sp": false});
            //id
            this.dropData.provinceId.push({"id": newData[o][0]["provinceId"]});
            //name
            this.dropData.selectedProvince.push({"name": newData[o][0]["provinceName"]});
            //
            this.dropData.monTypePause.push({"mp": false});
            //id
            this.dropData.monTypeModel.push([]);
            //
            this.dropData.penShow.push({"ps": true});
            //
            this.dropData.selectSchoolModelDuo.push({"sm": []});
            //
            this.dropData.linkManModel.push({"link": []});
            //
            this.ed.push({
              shCheck: true,
              startTime: '',
              endTime: '',
              glf: '',
              yf: '',
              errorTip: '',
              popd: '',
              popdK: ''
            });
            this.tableData.push({
                    sun: []  
                  });
            for(var e=0;e<newData[o].length;e++){
              //id
              this.dropData.monTypeModel[oo].push(newData[o][e]["costTypeId"]);
              //
              var schoolListName = [];
              if(newData[o][e]["costTypeId"] == 2){  //
                for(var s=0;s<newData[o][e]["costDetailCustomerVos"].length;s++){
                  this.dropData.selectSchoolModelDuo[oo]["sm"].push(newData[o][e]["costDetailCustomerVos"][s]["customerId"]);
                  schoolListName.push(newData[o][e]["costDetailCustomerVos"][s]["customerName"]);
                }
                //
                this.dropData.linkManModel[oo]["link"].push({"lk": ""});
                //ed
                this.ed[oo].startTime = newData[o][e]["costSubsidySatrtDateString"];
                this.ed[oo].endTime = newData[o][e]["costSubsidyEndDateString"];
                this.ed[oo].glf = newData[o][e]["costTollMoney"];
                this.ed[oo].yf = newData[o][e]["costOilMoney"];
                this.ed[oo].popd = e;
                this.ed[oo].popdK = oo;

                var schList = this.dropData.schoolList;
                
                //tableData
              this.tableData[oo]["sun"].push({
                        "c1": newData[o][e]["costTypeId"],
                        "c2": newData[o][e]["costTypeName"],
                        "c3": false,
                        "c4": this.dropData.selectSchoolModelDuo[oo]["sm"],
                        "c5": schoolListName,
                        "c6": false,
                        "c7": '',
                        "c8": '',
                        "c9": {"edval": newData[o][e]["quota"], edObj: this.ed[oo]},
                        "c10": newData[o][e]["quotaPurpose"],
                        "c11": newData[o][e]["target"],
                        "c12": newData[o][e]["cityId"]
                      });
              }else{
                //
                this.dropData.linkManModel[oo]["link"].push({"lk": newData[o][e]["costDetailCustomerVos"][0]["customerContactsId"]});

                //tableData
              this.tableData[oo]["sun"].push({
                        "c1": newData[o][e]["costTypeId"],
                        "c2": newData[o][e]["costTypeName"],
                        "c3": false,
                        "c4": newData[o][e]["costDetailCustomerVos"][0]["customerId"],
                        "c5": newData[o][e]["costDetailCustomerVos"][0]["customerName"],
                        "c6": false,
                        "c7": newData[o][e]["costDetailCustomerVos"][0]["customerContactsId"],
                        "c8": newData[o][e]["costDetailCustomerVos"][0]["contactsName"],
                        "c9": {"edval": newData[o][e]["quota"], edObj: {}},
                        "c10": newData[o][e]["quotaPurpose"],
                        "c11": newData[o][e]["target"],
                        "c12": newData[o][e]["cityId"]
                      });
              }
            }
          }
        }.bind(this),
        error: function(){
          console.log("");
        }
      });
    },
    costJudgementTime: function(){
      $.ajax({
        url: this.path + "costJudgementTime",
        type: "post",
        dataType: "json",
        data: {},
        success: function(data){
          if(data["code"] == -1){  //
            this.$refs.childAlert.show("25~31", function(){
              this.$router.push('/mainIndex/index');
            });
          }
        }.bind(this),
        error: function(){
          console.log("");
        }
      });
    },
    closeViewPop: function(){
      this.viewData.shCheck = true;
    },
    viewPersonApply: function(){           //
      var isCanSub = true;
      //
      var yData = this.tableData, yl = yData.length;
      //
      var p = this.dropData.provinceId, pl = this.dropData.provinceId.length;
      for(var i=0;i<pl;i++){
        if(!p[i]["id"]){
          this.$refs.childAlert.show("" + (i+1) + "");
          isCanSub = false;
          break;
        }
      }
      if(!isCanSub){
        return false;
      }
      for(var j=0;j<yl;j++){
        var eveSun = yData[j]["sun"];
        for(var v=0;v<eveSun.length;v++){
          //
          if(!eveSun[v].c1){
            this.$refs.childAlert.show("" + (j+1) + "");
            isCanSub = false;
            break;
          }
          //
          if(!eveSun[v].c5){
            this.$refs.childAlert.show("" + (j+1) + ""+(v+1)+"");
            isCanSub = false;
            break;
          }
          //
          if(eveSun[v].c1!=2 && !eveSun[v].c7){
            this.$refs.childAlert.show("" + (j+1) + ""+(v+1)+"");
            isCanSub = false;
            break;
          }
          //
          if(!eveSun[v].c9.edval){
            this.$refs.childAlert.show("" + (j+1) + ""+(v+1)+"");
            isCanSub = false;
            break;
          }
          //
          if(!eveSun[v].c10){
            this.$refs.childAlert.show("" + (j+1) + ""+(v+1)+"");
            isCanSub = false;
            break;
          }
        }
        if(!isCanSub){
          break;
        }
      }
      if(!isCanSub){
        return false;
      }
      //
      var aList = this.searchData.areaList, aId = this.searchData.areaVal;
      for(var a=0;a<aList.length;a++){
        if(aList[a]["areaId"] == aId){
          this.viewData.areaData = aList[a]["areaName"];
        }
      }
      //
      $.ajax({
        url: this.path + "findUserProvinceList",
        type: "get",
        dataType: "json",
        data: {
          "jobNumber": localStorage.getItem("jobNumber"),
          "sessionIdOa": localStorage.getItem("sessionIdOa")          
        },
        success: function(data){
          this.viewData.personProvince = data["data"][0];
        }.bind(this),
        error: function(){
          console.log("");
        }
      });

      this.viewData.shCheck = false;
    },
    subPersonApply: function(){            //
      var isCanSub = true;
      //
      var yData = this.tableData, yl = yData.length;
      //
      var p = this.dropData.provinceId, pl = this.dropData.provinceId.length;
      for(var i=0;i<pl;i++){
        if(!p[i]["id"]){
          this.$refs.childAlert.show("" + (i+1) + "");
          isCanSub = false;
          break;
        }
      }
      if(!isCanSub){
        return false;
      }
      for(var j=0;j<yl;j++){
        var eveSun = yData[j]["sun"];
        for(var v=0;v<eveSun.length;v++){
          //
          if(!eveSun[v].c1){
            this.$refs.childAlert.show("" + (j+1) + "");
            isCanSub = false;
            break;
          }
          //
          if(!eveSun[v].c5){
            this.$refs.childAlert.show("" + (j+1) + ""+(v+1)+"");
            isCanSub = false;
            break;
          }
          //
          if(eveSun[v].c1!=2 && !eveSun[v].c7){
            this.$refs.childAlert.show("" + (j+1) + ""+(v+1)+"");
            isCanSub = false;
            break;
          }
          //
          if(!eveSun[v].c9.edval){
            this.$refs.childAlert.show("" + (j+1) + ""+(v+1)+"");
            isCanSub = false;
            break;
          }
          //
          if(!eveSun[v].c10){
            this.$refs.childAlert.show("" + (j+1) + ""+(v+1)+"");
            isCanSub = false;
            break;
          }
        }
        if(!isCanSub){
          break;
        }
      }
      if(!isCanSub){
        return false;
      }

      //
      var subDetailString = "[";
      for(var s=0;s<yl;s++){
        var eveSub = yData[s]["sun"];
        for(var e=0;e<eveSub.length;e++){
          subDetailString += "{";

          subDetailString += "\"costTypeId\":\""+ eveSub[e]["c1"] +"\",";
          subDetailString += "\"areaId\":\""+ this.searchData.areaVal +"\",";
          subDetailString += "\"provinceId\":\""+ this.dropData.provinceId[s]["id"] +"\",";
          subDetailString += "\"cityId\":\""+ eveSub[e]["c12"] +"\",";
          subDetailString += "\"quota\":\""+ eveSub[e]["c9"]["edval"] +"\",";
          subDetailString += "\"quotaPurpose\":\""+ eveSub[e]["c10"] +"\",";
          subDetailString += "\"targe\":\""+ eveSub[e]["c11"] +"\",";
          if(eveSub[e]["c1"] == 2){   //
            subDetailString += "\"costOilMoney\":\""+ eveSub[e]["c9"]["edObj"]["yf"] +"\",";
            subDetailString += "\"costTollMoney\":\""+ eveSub[e]["c9"]["edObj"]["glf"] +"\",";
            subDetailString += "\"costSubsidySatrtDateString\":\""+ eveSub[e]["c9"]["edObj"]["startTime"] +"\",";
            subDetailString += "\"costSubsidyEndDateString\":\""+ eveSub[e]["c9"]["edObj"]["endTime"] +"\",";

            
            subDetailString += "\"customerList\":";
            subDetailString += "[";
            for(var v=0;v<eveSub[e]["c4"].length;v++){
              subDetailString += "{";
              subDetailString += "\"customerId\":\""+ eveSub[e]["c4"][v] +"\"";
              subDetailString += "}";
              if(v!=(eveSub[e]["c4"].length-1)){
                subDetailString += ",";
              }
            }
            subDetailString += "],";
          }else{
            subDetailString += "\"customerList\":";
            subDetailString += "[{";
            subDetailString += "\"customerId\":\""+ eveSub[e]["c4"] +"\",";
            subDetailString += "\"customerContactsId\":\""+ eveSub[e]["c7"] +"\"";
            subDetailString += "}],";
          }
          subDetailString += "\"sign\":\""+ s +"\"";
          subDetailString += "}";

          if(s==(yl-1)){       //
            if(e != (eveSub.length-1)){
              subDetailString += ",";
            }
          }else{
            subDetailString += ",";
          }
        }
      }
      subDetailString += "]";

      var subApplyData = {};
      if(this.costId){                     //
        subApplyData = {
          "jobNumber": localStorage.getItem("jobNumber"),
          "sessionIdOa": localStorage.getItem("sessionIdOa"),
          "spendtime": this.searchData.searchDateVal+"-01",
          "money": this.subMoney,
          "detail": subDetailString,
          "costId": this.costId
        }
      }else{
        subApplyData = {
          "jobNumber": localStorage.getItem("jobNumber"),
          "sessionIdOa": localStorage.getItem("sessionIdOa"),
          "spendtime": this.searchData.searchDateVal+"-01",
          "money": this.subMoney,
          "detail": subDetailString
        }
      }

      //
      if(!this.subPause){
        return false;
      }
      this.subPause = false;

      $.ajax({
        url: this.path + "insertPersonalOverallPlanning",
        type: "post",
        dataType: "json",
        data: subApplyData,
        success: function(data){
          if(data["code"] == 200){
            if(this.costId){
              this.$refs.childAlert.show("", function(){
                this.$router.push('/mainIndex/moneyApply/ProgressSearch');
              });
            }else{
              this.$refs.childAlert.show("", function(){
                this.$router.push('/mainIndex/moneyApply/ProgressSearch');
              });
            }
          }else{
            this.$refs.childAlert.show(data["msg"]);
            this.subPause = true;
          }
        }.bind(this),
        error: function(){
          this.subPause = true;
          console.log("");
        }.bind(this)
      });
    },
    removeTableData: function(k){          //
      this.tableData.splice(k, 1);
      this.dropData.provincePause.splice(k, 1);
      this.dropData.provinceId.splice(k, 1);
      this.dropData.selectedProvince.splice(k, 1);
      this.dropData.monTypePause.splice(k, 1);
      this.dropData.monTypeModel.splice(k, 1);
      this.dropData.penShow.splice(k, 1);
      //this.dropData.selectSchoolModel.push({"sm": [{"sg":''}]});
      this.dropData.selectSchoolModelDuo.splice(k, 1);
      this.dropData.linkManModel.splice(k, 1);
      this.ed.splice(k, 1);
    },
    addNewTableData: function(){          //
      this.tableData.push({
        "sun": [
                {
                  "c1": '',
                  "c2": '',
                  "c3": false,
                  "c4": '',
                  "c5": '',
                  "c6": false,
                  "c7": '',
                  "c8": '',
                  "c9": {"edval": '', edObj: {}},
                  "c10": '',
                  "c11": '',
                  "c12": ''
                }
              ]
      });
      this.dropData.provincePause.push({"sp":false});
      this.dropData.provinceId.push({"id": ''});
      this.dropData.selectedProvince.push({"name": ''});
      this.dropData.monTypePause.push({"mp": false});
      this.dropData.monTypeModel.push([]);
      this.dropData.penShow.push({"ps": false});
      //this.dropData.selectSchoolModel.push({"sm": [{"sg":''}]});
      this.dropData.selectSchoolModelDuo.push({"sm": []});
      this.dropData.linkManModel.push({"link": [{"lk": ''}]});

      this.ed.push({
        shCheck: true,
        startTime: '',
        endTime: '',
        glf: '',
        yf: '',
        errorTip: '',
        popd: '',
        popdK: ''
      });
    },
    countEduFee: function(i){
      if(!this.ed[i].startTime){
        this.ed[i].errorTip = "";
        return false;
      }
      if(!this.ed[i].endTime){
        this.ed[i].errorTip = "";
        return false;
      }
      var st = this.ed[i].startTime, et = this.ed[i].endTime;
      st = st.split("-").join(""), et = et.split("-").join("");
      st = Number(st), et = Number(et);
      if(st > et){
        this.ed[i].errorTip = "";
        return false;
      }
      if(!this.ed[i].glf){
        this.ed[i].errorTip = "";
        return false;
      }
      if(!this.ed[i].yf){
        this.ed.errorTip = "";
        return false;
      }
      this.ed[i].errorTip = "";

      this.ed[i].shCheck = true;
      //,
      var stTime = new Date(this.ed[i].startTime).getTime(), etTime = new Date(this.ed[i].endTime).getTime();
      var days = ((etTime - stTime)/(1000*60*60*24));
      days += 1; //1
      //
      var fuI = this.ed[i].popd;
      var fuK = this.ed[i].popdK;
      this.tableData[fuK]["sun"][fuI].c9.edval = Number(this.ed[i].glf) + Number(this.ed[i].yf) + days*this.ccbz;
      this.tableData[fuK]["sun"][fuI].c9.edObj = this.ed[i];

      this.countMoney();
    },
    glfKeyup: function(i){
      if(isNaN(this.ed[i].glf) || Number(this.ed[i].glf) <= 0){
        this.ed[i].glf = '';
      }
    },
    yfKeyup: function(i){
      if(isNaN(this.ed[i].yf) || Number(this.ed[i].yf) <= 0){
        this.ed[i].yf = '';
      }
    },
    spanChailv: function(i, k){
      this.ed[k].shCheck = false;
      this.ed[k].popd = i;
      this.ed[k].popdK = k;
    },
    canelEdu: function(){
      this.ed.shCheck = true;
    },
    valiEdu: function(t){
      if(isNaN(t.c9.edval) || Number(t.c9.edval) <= 0){
        t.c9.edval = '';
      }
      this.countMoney();
    },
    spanShowSchool: function(i, k){
      //
      for(var c=0;c<this.tableData.length;c++){
        for(var r=0;r<this.tableData[c]["sun"].length;r++){
          this.tableData[c]["sun"][r]["c3"] = false;
        }
      }

      this.tableData[k]["sun"][i]["c3"] = true;
      //
      this.dropData.cityList = this.OACommon.findCitiesList(this.dropData.provinceId[k]["id"]);

      //console.log(this.tableData[k]["sun"][i]["c12"]);
      if(this.tableData[k]["sun"][i]["c12"]){
        this.getSchoolList(k, i);
      }
    },
    spanLinkMan: function(i, k){
      //
      var schId = this.tableData[k]["sun"][i]["c4"];
      $.ajax({
        url: this.path + "findCustomerContactsAllByCustomerId",
        type: "post",
        dataType: "json",
        data: {"customerId": schId},
        success: function(data){
          data = data["data"];
          if(data.length > 0){
            this.dropData.linkManList = data;
          }else{
            this.dropData.linkManList = [{"customerContactsId": "", "contactsName": ""}];
          }

          this.tableData[k]["sun"][i]["c6"] = true;
        }.bind(this),
        error: function(){
          console.log("");
        }
      });
    },
    getSchoolList: function(k, i){             //
      var cityId = this.tableData[k]["sun"][i]["c12"];
      $.ajax({
        url: this.path + "findAllCustomerBycitiesId",
        type: "post",
        dataType: "json",
        data: {"citiesId": cityId},
        success: function(data){
          data = data["data"];
          if(data.length == 0){
            this.dropData.schoolList = [{"customerId":'', "customerName":""}];
          }else{
            this.dropData.schoolList = data;
          }
        }.bind(this),
        error: function(){
          console.log("");
        }
      });
    },
    fuSchoolVal: function(i, k){
      if(this.tableData[k]["sun"][i].c1 == 2){   //schoolModel
        this.tableData[k]["sun"][i].c4 = this.dropData.selectSchoolModelDuo[k]["sm"];
      }
      /*else{
        this.tableData[k]["sun"][i].c4 = this.dropData.selectSchoolModel[k]["sm"][i]["sg"];
      }*/
      
      var schList = this.dropData.schoolList;
      if(this.tableData[k]["sun"][i].c1 == 2){  //schoolModel
        var secSchList = [], schModel = this.dropData.selectSchoolModelDuo[k]["sm"]; 
        for(var j=0;j<schList.length;j++){
          for(var n=0;n<schModel.length;n++){
            if(schList[j]["customerId"] == schModel[n]){
              secSchList.push(schList[j]["customerName"]);
            }
          }
        }
        this.tableData[k]["sun"][i].c5 = secSchList;
      }else{
        for(var j=0;j<schList.length;j++){
          if(schList[j]["customerId"] == this.tableData[k]["sun"][i].c4){
            this.tableData[k]["sun"][i].c5 = schList[j]["customerName"];
            break;
          }
        }
      }
      this.tableData[k]["sun"][i].c3 = false;
      this.dropData.cityList = [];
      this.dropData.schoolList = [];
    },
    fuLinkMan: function(i, k){          //
      //this.tableData[k]["sun"][i].c7 = this.dropData.linkManModel[k]["link"][i]["lk"];
      
      var linkMan = this.tableData[k]["sun"][i].c7;
      var linkList = this.dropData.linkManList;
      if(linkMan != ''){
        for(var j=0;j<linkList.length;j++){
          if(linkList[j]["customerContactsId"] == linkMan){
            this.tableData[k]["sun"][i].c8 = linkList[j]["contactsName"];
          }
        }
      }

      this.tableData[k]["sun"][i].c6 = false;
    },
    getSelectCostType: function(){
      this.dropData.monTypeList = this.OACommon.selectCostType();
    },
    setSearchData: function(){
      var date = new Date();
      var year = date.getFullYear(), month = (date.getMonth()+1);
      var searchDate = [];
      searchDate.push(year + "-" + ((month<10)?("0"+month):month));
      if(month == 12){
        searchDate.push((year+1) + "-01");
      }else{
        month += 1;
        searchDate.push(year + "-" + ((month<10)?("0"+month):month));
      }
      this.searchData.searchDate = searchDate;
      this.searchData.searchDateVal = searchDate[0];
      //
      var areaList = this.OACommon.findAreaByCondition();
      areaList.shift();
      this.searchData.areaList = areaList;
      this.searchData.areaVal = areaList[0]["areaId"];
    },
    getProvinceList: function(){
      var pList = this.OACommon.findProvinceList(this.searchData.areaVal);
      pList.shift();
      this.dropData.provinceList = pList;
    },
    showProList: function(k){
      if(this.dropData.monTypeModel[k].length == 0){   //
        this.dropData.provincePause[k].sp = true;
      }
    },
    hideProList: function(k){
      this.dropData.provincePause[k].sp = false;
    },
    showMonTypeList: function(k){
      //
      if(this.dropData.selectedProvince[k].name != ''){            
        this.dropData.monTypePause[k].mp = true;
      }
    },
    hideMonTypeList: function(k){
      this.dropData.monTypePause[k].mp = false;
      // tableData 
      var typeList = this.dropData.monTypeList;

      if(this.dropData.monTypeModel[k].length > 0){
        var moneyTypeArr = this.dropData.monTypeModel[k];

        if(!this.tableData[k]["sun"][0]["c1"]){   //
          this.tableData[k]["sun"] = [];
          for(var i=0;i<moneyTypeArr.length;i++){
            this.tableData[k]["sun"].push({
                  "c1": moneyTypeArr[i],
                  "c2": getMoneyType(moneyTypeArr[i]),
                  "c3": false,
                  "c4": '',
                  "c5": '',
                  "c6": false,
                  "c7": '',
                  "c8": '',
                  "c9": {"edval": '', edObj: {}},
                  "c10": '',
                  "c11": '',
                  "c12": ''
                });
          }
        }else{                               //
          for(var j=0;j<moneyTypeArr.length;j++){
            this.tableData[k]["sun"].push({
                "c1": moneyTypeArr[j],
                "c2": getMoneyType(moneyTypeArr[j]),
                "c3": false,
                "c4": '',
                "c5": '',
                "c6": false,
                "c7": '',
                "c8": '',
                "c9": {"edval": '', edObj: {}},
                "c10": '',
                "c11": '',
                "c12": ''
              });
          }

          var newTableData = {}, td = this.tableData[k]["sun"];
          for(var t=0;t<td.length;t++){
            var c1 = td[t]["c1"];
            if(!newTableData[c1]){
              newTableData[c1] = td[t];
            }
          }

          //tableData
          this.tableData[k]["sun"] = [];
          for(var m=0;m<moneyTypeArr.length;m++){
            var eveData = moneyTypeArr[m];
            this.tableData[k]["sun"].push(newTableData[eveData]);
          }
        }
        
        this.dropData.penShow[k].ps = true;
      }else{
        this.tableData[k].sun = [{
            "c1": '',
            "c2": '',
            "c3": false,
            "c4": '',
            "c5": '',
            "c6": false,
            "c7": '',
            "c8": '',
            "c9": {"edval": '', edObj: {}},
            "c10": '',
            "c11": '',
            "c12": ''
        }];

        this.dropData.penShow[k].ps = false;
      }

      //id
      function getMoneyType(id){
        var typeName = '';
        for(var i=0;i<typeList.length;i++){
          if(typeList[i]["costTypeId"] == id){
            typeName = typeList[i]["costTypeName"];
            break;
          }
        }
        return typeName;
      }
    },
    setSecProvinceId: function(k, pid, province){
      this.dropData.provinceId[k] = [];
      this.dropData.selectedProvince[k] = [];
      this.dropData.provinceId[k] ={"id": pid};
      this.dropData.selectedProvince[k] = {"name": province};
      this.dropData.provincePause[k].sp = false;
    },
    countMoney: function(){
      var money = 0;
      var yData = this.tableData, yl = yData.length;
      for(var j=0;j<yl;j++){
        var eveSun = yData[j]["sun"];
        for(var v=0;v<eveSun.length;v++){
          //console.log(eveSun[v]["c9"]);
          money += Number(eveSun[v]["c9"]["edval"]);
        }
      }
      this.subMoney = money;
    }
  }
}
</script>
<style>
	table tr td{position:relative;}
  .person_ul{left:0;}
  .person_ul>li{color:#333;text-align:center;}
  .person_ul>li input[type=checkbox]{margin-left:-10px;}
  .person_ul>li label{margin-left:10px;}
  ul.edu{background:#DDD;}
  ul.edu{border:1px solid #BBB;}
  ul.edu>li{text-align:left;}
  ul.edu>li input[type=checkbox], ul.edu>li input[type=radio]{margin-left:25px;}
  ul.edu>li input[type=radio]{width:14px;height:14px;vertical-align:middle;}
  .xiaopen{width:20px;height:20px;cursor:pointer;position:absolute;right:0;bottom:0;color:blue;}
  textarea.tt{width:100%;height:70px;}
  .el-date-editor.el-input, .el-date-editor.el-input__inner{width:170px;cursor:pointer;}
  .el-input__inner{height:30px;line-height:30px;cursor:pointer;border:1px solid #C9C9C9;}
  .el-input__icon{line-height:30px;}

  .view_eve_table_div{margin:10px 20px 0;}
  .view_eve_table_div table{width:100%;border-collapse:collapse;border:1px solid #d9d9d9;}
  .view_eve_table_div table thead{background:#00A1E9;color:#FFF;}
  .view_eve_table_div table td{text-align:center;vertical-align:middle;padding:3px 2px;}
  .view_eve_table_div table tbody td{font-size:12px;}
</style>




