<template>
  <div class="hello">
    <div class="gonggao_bread">
        <a href="#" class="fa"></a>
        &gt;
        <span></span>
        &gt;
        <span></span>
      </div>
      <div class="search_div">
        <div>
          <label style="width:60px;"></label>
          <el-date-picker
            v-model="searchData.startDate"
            type="date"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            placeholder="">
          </el-date-picker>

          <span class="zhi"></span>

          <el-date-picker
            v-model="searchData.endDate"
            type="date"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            placeholder="">
          </el-date-picker>
          <label></label>
          <select v-model="searchData.subjectId">
            <option v-for="s in searchData.subject" v-bind:value="s.subjectId">{{ s.subjectName }}</option>
          </select>
          <label></label>
          <input type="text" v-model="searchData.teacherName"/>
          <span class="btn-blue-s" v-on:click="getTeacherList"></span>
          <span class="btn-yellow-s" v-on:click="showAddView"></span>
        </div>
      </div>
      <div class="gonggao_body table_body">
        <div class="data_table_head">
          <ul>
            <li class="td10 xh"></li>
            <li class="td14"></li>
            <li class="td14"></li>
            <li class="td14"></li>
            <li class="td14"></li>
            <li class="td16"></li>
            <li class="td16"></li>
          </ul>
        </div>
        <div class="data_table_body">
          <ul v-for="(l, i) in list" v-if="list.length>0">
            <li class="td10 xh">{{ i+1 }}</li>
            <li class="td14">{{ l.createTime }}</li>
            <li class="td14">{{ l.teacherName }}</li>
            <li class="td14">{{ l.subjectName }}</li>
            <li class="td14">{{ l.oneHourMoney }}</li>
            <li class="td16">
              <a href="javascript:void(0);" class="table_pa" v-on:click="showRemark(l.remark)"></a>
            </li>
            <li class="td16">
              <a href="javascript:void(0);" class="table_pa" v-on:click="updateTeacher(l.teacherId)"></a>
              <a href="javascript:void(0);" class="table_la" v-on:click="deleteTeacher(l.teacherId)"></a>
            </li>
          </ul>
          <ul v-if="list.length==0">
            <li style="width:100%;text-align:center;color:red;"></li>
          </ul>
        </div>
      </div>

      <oa-page ref="childPage"></oa-page>

      <div class="bgpop" v-if="viewData.isShow"></div>
      <div class="kecha_pop" style="width:450px;height:405px;" v-if="viewData.isShow">
        <p class="pop_tit"></p>
        <div class="pop_form_div">
          <div class="form_eve">
            <label class="label_tit-4"></label>
            <input type="text" class="form_input-9" v-model="viewData.teacherName">
          </div>
          <div class="form_eve">
            <label class="label_tit-4"></label>
            <input type="text" class="form_input-9" v-model="viewData.money" v-on:blur="moneyBlur">
          </div>
          <div class="form_eve">
            <label class="label_tit-4"></label>
            <select v-model="viewData.subjectId" class="form_input-9">
              <option v-for="s in searchData.subject" v-bind:value="s.subjectId">{{ s.subjectName }}</option>
            </select>
          </div>
          <div class="form_eve">
            <label class="label_tit-4">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</label>
            <textarea class="form_input-10" maxlength="300" v-model="viewData.remark"></textarea>
          </div>
          <div class="err_tip">{{ viewData.errTip }}</div>
          <div class="fbtong_btn_div" style="margin-top:10px;">
            <span class="btn-blue-s" v-on:click="closeView"></span>
            <span class="btn-blue-s" v-on:click="subView"></span>
          </div>
        </div>
      </div>

      <div class="bgpop" v-if="remark.isShow"></div>
      <div class="kecha_pop" style="width:500px;height:210px;" v-if="remark.isShow">
        <table cellpadding="0" cellspacing="0" style="border:0;">
          <tr>
            <td class="head"></td>
          </tr>
          <tr class="x">
            <td style="height:70px;padding:0 20px;">{{ remark.remark }}</td>
          </tr>
        </table>
        <div style="text-align:center;margin-top:20px;">
          <span class="btn-blue" v-on:click="closeRemark"></span>
        </div>
      </div>

      <oa-alert ref="childAlert"></oa-alert>
      <oa-confirm ref="childConfirm"></oa-confirm>
  </div>
</template>

<script>
import $ from 'jquery'
import oaPage from '@/components/commonVue/Page'
import oaAlert from '@/components/commonVue/Alert'
import oaConfirm from '@/components/commonVue/Confirm'

export default {
  name: 'teacher',
  components: {
      'oa-page': oaPage,
      'oa-alert': oaAlert,
      'oa-confirm': oaConfirm
    },
  data () {
    return {
      path: this.OACommon["publicDef"]["qpath"],
      searchData: {
        startDate: '',
        endDate: '',
        subject: [],
        subjectId: '',
        teacherName: ''
      },
      list: [],
      pageNum: 1,
      pageSize: 10,
      viewData: {
        isShow: false,
        errTip: '',
        subjectId: '',
        teacherName: '',
        money: '',
        remark: '',
        teacherId: '',
        subPause: true
      },
      remark: {
        isShow: false,
        remark: ''
      }
    }  
  },
  created: function () {
    this.setInitSearchData(); 
    this.getTeacherList();
  },
  methods: {
    deleteTeacher: function(id){
      this.$refs.childConfirm.show("", function(){
        $.ajax({
          url: this.path + "deleteTeacherByTeacherId",
          type: "post",
          dataType: "json",
          data: {
            "jobNumber": localStorage.getItem("jobNumber"),
            "sessionIdOa": localStorage.getItem("sessionIdOa"),
            "teacherId": id
          },
          success: function(data){
            if(data["code"] == 200){
              this.$refs.childAlert.show("");
              this.getTeacherList();
            }else{
              this.$refs.childAlert.show(data["msg"]);
            }
          }.bind(this),
          error: function(){
            console.log("");
          }
        });
      }.bind(this));
    },
    updateTeacher: function(id){
      this.viewData.teacherId = id;
      $.ajax({
        url: this.path + "findTeacherByTeacherId",
        type: "post",
        dataType: "json",
        data: {
          "jobNumber": localStorage.getItem("jobNumber"),
          "sessionIdOa": localStorage.getItem("sessionIdOa"),
          "teacherId": this.viewData.teacherId
        },
        success: function(data){
          data = data["data"];
          this.viewData.teacherName = data["teacherName"];
          this.viewData.money = data["oneHourMoney"];
          this.viewData.subjectId = data["subjectId"];
          this.viewData.remark = data["remark"];
        }.bind(this),
        error: function(){
          console.log("");
        }
      });
      this.viewData.isShow = true;
    },
    showRemark: function(rk){
      this.remark.remark = rk;
      this.remark.isShow = true;
    },
    closeRemark: function(){
      this.remark.isShow = false;
    },
    moneyBlur: function(){
      if(isNaN(this.viewData.money) || Number(this.viewData.money)<=0 || this.viewData.money.charAt(0)==0){
        this.viewData.money = "";
      }
    },
    subView: function(){
      if(!this.viewData.teacherName){
        this.viewData.errTip = "";
        return false;
      }
      if(!this.viewData.money){
        this.viewData.errTip = "";
        return false;
      }
      if(!this.viewData.subjectId){
        this.viewData.errTip = "";
        return false;
      }
      if(!this.viewData.remark){
        this.viewData.errTip = "";
        return false;
      }
      this.viewData.errTip = "";

      var subData = {};
      if(this.viewData.teacherId){           //
        subData = {
          "jobNumber": localStorage.getItem("jobNumber"),
          "sessionIdOa": localStorage.getItem("sessionIdOa"),
          "teacherName": this.viewData.teacherName,
          "oneHourMoney": this.viewData.money,
          "subjectId": this.viewData.subjectId,
          "remark": this.viewData.remark,
          "teacherId": this.viewData.teacherId
        };
      }else{
        subData = {
          "jobNumber": localStorage.getItem("jobNumber"),
          "sessionIdOa": localStorage.getItem("sessionIdOa"),
          "teacherName": this.viewData.teacherName,
          "oneHourMoney": this.viewData.money,
          "subjectId": this.viewData.subjectId,
          "remark": this.viewData.remark
        };
      }

      if(!this.viewData.subPause){
        return false;
      }
      this.viewData.subPause = false;

      $.ajax({
        url: this.path + "insertOrUpdateTeacher",
        type: "post",
        dataType: "json",
        data: subData,
        success: function(data){
          if(data["code"] == 200){
            if(this.viewData.teacherId){   //
              this.$refs.childAlert.show("");
            }else{
              this.$refs.childAlert.show("");
            }

            this.closeView();
            this.getTeacherList();
            this.viewData.subPause = true;
          }else{
            this.viewData.subPause = true;
            this.viewData.errTip = "";
          }
        }.bind(this),
        error: function(){
          this.viewData.subPause = true;
          console.log("");
        }
      });
    },
    closeView: function(){
      this.viewData.isShow = false;
      this.viewData.errTip = "";
      this.viewData.subjectId = "";
      this.viewData.teacherName = "";
      this.viewData.money = "";
      this.viewData.teacherId = "";
    },
    showAddView: function(){
      this.viewData.isShow = true;
    },
    getTeacherList: function(pn, ps){
      //pn:   ps
      if(pn && typeof(pn)!='object'){
        this.pageNum = pn;
      }
      if(ps && typeof(ps)!='object'){
        this.pageSize = ps;
      }

      $.ajax({
        url: this.path + "findTeacherListPage",
        type: "post",
        dataType: "json",
        data: {
          "jobNumber": localStorage.getItem("jobNumber"),
          "sessionIdOa": localStorage.getItem("sessionIdOa"),
          "teacherName": this.searchData.teacherName,
          "subjectId": this.searchData.subjectId,
          "startDate": this.searchData.startDate,
          "endDate": this.searchData.endDate,
          "pageNum": this.pageNum,
          "pageSize": this.pageSize
        },
        success: function(data){
          this.list = data["data"]["list"];

          this.$refs.childPage.setArg(data["data"]["pages"], this.pageNum, this.pageSize, this.getTeacherList);
        }.bind(this),
        error: function(){
          console.log("");
        }
      });
    },
    setInitSearchData: function(){
      //
      this.searchData.startDate = this.OACommon.getOADate(new Date()).prevMonthToday;
      this.searchData.endDate = this.OACommon.getOADate(new Date()).todayDate;
      this.searchData.subject = this.OACommon.findSubjectAll();
    }
  }
}
</script>
<style>
  .el-date-editor.el-input, .el-date-editor.el-input__inner{width:170px;cursor:pointer;}
  .el-input__inner{height:30px;line-height:30px;cursor:pointer;border:1px solid #C9C9C9;}
  .el-input__icon{line-height:30px;}

  .err_tip{height:20px;text-align:center;color:red;margin-top:10px;}
</style>




